import { Component } from "react";

class NotFound extends Component {
    render() {
        return (
            <div className="body">
                <div className="body__row-flex">
                    <div className=" body__row-flex--main">
                        <div className="body__notFound">
                            <div className="item__notification--notFound">
                                <div>
                                    <i class="fa-regular fa-link-slash"></i>
                                    <div>Trang không tồn tại</div>
                                </div>

                            </div>
                            <div className="item__notification--notFound">
                                <div className="text__notification--notFound">
                                    Có thể trang bạn đang truy cập đã bị gỡ bỏ hoặc
                                    đã sai đường dẫn.<br></br>
                                    Bạn hãy kiểm tra lại liên kết có chính xác không
                                </div>
                            </div>
                            <div className="item__notification--notFound">
                                <div>Đi tới Trang chủ</div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        )
    }
};
export default NotFound;